.location-search-input {
  width: 100%;
  margin-top: 2%;
  background-color: white;
  background-position: 10px 10px;
}

.ant-btn-primary {
  margin: 15px;
}

.button:hover {
  background-color: deepskyblue;
  font-weight: bold;
}

html {
  background-color: #fff;
  font-weight: 300;
  -webkit-font-smoothing: antialiased;
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  text-align: center;
}

.h1 {
  margin: 0px;
}

.googlemaps {
  height: 100% !important;
  width: 99% !important;
}

.flex-container {
  display: flex;
  align-items: stretch;
}

.flex-container-div-left{
  width: 100px;
  margin: 10px;
  text-align: center;
  flex-grow: 2;
}
.flex-container-div-right{

  width: 100px;
  margin: 10px;
  text-align: center;
  line-height: 10px;
  font-size: 10px;
  flex-grow: 8;
  flex-wrap: wrap;
    position: relative;
}

.flex-container-div-right-up{
    position: absolute;
   width: 100px;
   margin: 10px;
   text-align: center;
   line-height: 10px;
   font-size: 10px;
   flex-grow: 8;
   align-items: flex-start;
 }

.flex-container-div-right-down{
  align-content: flex-end;
}

.sort-by{
    color: #000;
    cursor: default;
}
.sort-by:hover{
    color: #000;
}

.save-trip-button{
    width: 100%;
}

.ant-input {
    margin-top: 15px !important;
    margin-bottom: 5px;
}

.ant-card-bordered {
    margin-left: 10px;
    margin-bottom: 10px;
}

.ant-card-body {
    margin-left:0px !important;
}

.bottom-button {
    height: 50px;
    width: 25%;
}

.spotifunk-button {
    height: 50px;
    width: 25%;
}