body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.trips-container{
    display:-webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;

}

.trip-item{
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 20px;
    padding-right: 20px;
    width: 325px;
}

.spotify {
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 20px;
    padding-right: 20px;
}
.location-search-input {
  width: 100%;
  margin-top: 2%;
  background-color: white;
  background-position: 10px 10px;
}

.ant-btn-primary {
  margin: 15px;
}

.button:hover {
  background-color: deepskyblue;
  font-weight: bold;
}

html {
  background-color: #fff;
  font-weight: 300;
  -webkit-font-smoothing: antialiased;
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  text-align: center;
}

.h1 {
  margin: 0px;
}

.googlemaps {
  height: 100% !important;
  width: 99% !important;
}

.flex-container {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: stretch;
          align-items: stretch;
}

.flex-container-div-left{
  width: 100px;
  margin: 10px;
  text-align: center;
  -webkit-flex-grow: 2;
          flex-grow: 2;
}
.flex-container-div-right{

  width: 100px;
  margin: 10px;
  text-align: center;
  line-height: 10px;
  font-size: 10px;
  -webkit-flex-grow: 8;
          flex-grow: 8;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
    position: relative;
}

.flex-container-div-right-up{
    position: absolute;
   width: 100px;
   margin: 10px;
   text-align: center;
   line-height: 10px;
   font-size: 10px;
   -webkit-flex-grow: 8;
           flex-grow: 8;
   -webkit-align-items: flex-start;
           align-items: flex-start;
 }

.flex-container-div-right-down{
  -webkit-align-content: flex-end;
          align-content: flex-end;
}

.sort-by{
    color: #000;
    cursor: default;
}
.sort-by:hover{
    color: #000;
}

.save-trip-button{
    width: 100%;
}

.ant-input {
    margin-top: 15px !important;
    margin-bottom: 5px;
}

.ant-card-bordered {
    margin-left: 10px;
    margin-bottom: 10px;
}

.ant-card-body {
    margin-left:0px !important;
}

.bottom-button {
    height: 50px;
    width: 25%;
}

.spotifunk-button {
    height: 50px;
    width: 25%;
}
.stop-div{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-grow: 1;
            flex-grow: 1;
    margin-bottom: 10px;
}

.stop-item-right {
    -webkit-flex-grow: 8;
            flex-grow: 8;
}

.steps-card{
    overflow-y: auto;
    max-height: 600px;
}

